
  
  

  .note-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 25px;
  }
  
  .circle {
    position: relative;
  }
  
  .circle__svg {
    transform: rotate(-90deg);
  }
  
  .circle__progress {
    fill: none;
    stroke-width: 5;
    stroke-opacity: 0.3;
    stroke-linecap: round;
  }
  
  .circle__progress--fill {
    --initialStroke: 0;
    --transitionDuration: 0;
    stroke-opacity: 1;
    stroke-dasharray: var(--initialStroke);
    stroke-dashoffset: var(--initialStroke);
    transition: stroke-dashoffset var(--transitionDuration) ease;
  }
  
  .note-display:nth-child(1) .circle__progress { stroke: rgb(14, 14, 14); }
  /* .note-display:nth-child(2) .circle__progress { stroke: #FF00AA; }
  .note-display:nth-child(3) .circle__progress { stroke: #AA00FF; }
  .note-display:nth-child(4) .circle__progress { stroke: #00AAFF; } */
  
  .percent {
    width: 100%;
    top: 50%;
    left: 50%;
    position: absolute;
    font-weight: bold;
    text-align: center;
    line-height: 28px;
    transform: translate(-50%, -50%);
  }
  
  .percent__int { font-size: 28px; }
  .percent__dec { font-size: 12px; }
  
  .label {
    font-family: 'Raleway', serif;
    font-size: 14px;
    text-transform: uppercase;
    margin-top: 15px;
  }